<template>
    <div id="list">
        <div class="row">
            <div class="col search-row">
                <span class="p-input-icon-left me-2">
                    <i class="pi pi-search" />
                    <Word
                        v-model="searchText"
                        @keydown.enter="search"
                        placeholder="Search"
                        class="search-input"
                    />
                </span>
                <Button
                    label="Search"
                    class="p-button-secondary"
                    @click="search"
                />
            </div>
        </div>

        <div class="row last20-row">
            <div class="col">
                <router-link :to="'/list/today'">Today</router-link> |
                <router-link :to="'/list/ytd'">Yesterday</router-link> |
                <router-link :to="'/list/last20'">Last 20 saved</router-link> |
                <template v-for="num in 26" :key="num">
                    <router-link :to="'/list/alphabet/' + alphabet(num)">
                        {{ alphabet(num) }}
                    </router-link>
                    <template v-if="num != 26"><span> | </span></template>
                </template>
            </div>
        </div>

        <hr />

        <template v-for="(doc, index) in filterData" :key="index">
            <Document :data="doc" />

            <div class="row">
                <div class="col col-sm-i">
                    save: {{ doc.save_date + " " + doc.save_time }} | next:
                    {{ doc.next_review }}
                    <template v-if="doc.last_review">
                        | last: {{ doc.last_review }}
                    </template>
                </div>
            </div>

            <hr class="hr-dashed" />
        </template>
    </div>
    <Toast />
</template>

<script lang="ts">
import { defineComponent } from "vue";
import { RouteParams } from "vue-router";
import { ActiveLoader } from "vue-loading-overlay";

import { getList } from "@/firebase";

import Word from "@/components/Word.vue";
import Document from "@/components/Document.vue";

var loader: ActiveLoader;

export default defineComponent({
    components: {
        Word,
        Document,
    },
    methods: {
        loadList(params: RouteParams) {
            this.showLoading();

            let type = params.type as string;
            let text = params.text as string;

            if (!type) type = "today";

            if (type == "search" && text != this.searchText)
                this.searchText = text;

            if (
                ["today", "ytd", "last20"].includes(type) ||
                (["search", "alphabet"].includes(type) && text)
            ) {
                if (
                    this.lastType == "" || // init
                    (type != this.lastType && // not same data
                        (!["search", "alphabet"].includes(type) || // previous and current not search and balphabet
                            !["search", "alphabet"].includes(this.lastType)))
                ) {
                    getList(type)
                        .then((querySnapshot) => {
                            this.data = querySnapshot.docs.map((doc: any) => {
                                return doc.data();
                            });

                            this.setFilterData(type, text);
                        })
                        .catch((err) => {
                            console.log(err);

                            this.errorToast("Error while loading list.");
                        })
                        .finally(() => {
                            this.hideLoading();
                        });

                    this.lastType = type;
                } else {
                    this.setFilterData(type, text);
                    this.hideLoading();
                }
            }
        },
        alphabet(index: number) {
            return String.fromCharCode(index + 64);
        },
        search() {
            if (this.searchText)
                this.$router.push("/list/search/" + this.searchText);
        },
        setFilterData(type: string, text: string) {
            if (["today", "ytd", "last20"].includes(type)) {
                this.filterData = this.data;
            } else if (type == "search") {
                this.filterData = this.data.filter((doc: any) =>
                    doc.word.toLowerCase().includes(text.toLowerCase())
                );
            } else if (type == "alphabet") {
                this.filterData = this.data.filter((doc: any) =>
                    doc.word.toLowerCase().startsWith(text.toLowerCase())
                );
            }
        },
        showLoading() {
            loader = this.$loading.show({ color: "#198964" });
        },
        hideLoading() {
            loader.hide();
        },
        errorToast(detail: string) {
            this.$toast.add({
                severity: "error",
                summary: "Error",
                detail: detail,
                life: 3000,
            });
        },
    },
    data() {
        return {
            data: [],
            filterData: [],
            lastType: "",
            searchText: "",
        };
    },
    mounted() {
        this.loadList(this.$route.params);
    },
    beforeRouteUpdate(to) {
        this.loadList(to.params);
    },
});
</script>

<style scoped lang="less">
#list {
    .search-row {
        display: flex;
        align-content: center;

        .search-input {
            padding-left: 2rem;
        }
    }

    .last20-row {
        margin-top: 0.5rem !important;
    }

    .hr-dashed {
        border-top: 1px black dashed;
        background-color: transparent;
    }
}
</style>
